*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.err{
  height:100vh;
  width:100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ccc;
  font-size: 45px;
}
.signup-container, .login-container{
  height:100vh;
  width:100vw;
  display: flex;
  justify-content: center;
  align-items: center;
background-color: #ccc;
}
.signup-in, .login-in{
  height:85%;
  width:40%;
  display: flex;
  justify-content:space-between;
  align-items: center;
  flex-direction: column;
  border-radius: 25px;
 background-color:white;
}
.signup-in h1, .login-in h1{
  height:25%;
  width:100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.signup-in form, .login-in form{
  height:75%;
  width:80%;
  display:flex;
  justify-content:space-evenly;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.signup-in form input,.login-in form input {
  height:18%;
  width:100%;
  margin-bottom: 20px;
  font-size: 18px;
  padding-left: 10px;
  border-radius: 15px;
  background-color:rgb(238, 238, 238) ;
  border: none;
  outline: none;
}
.signup-in form button, .login-in form button{
  height:13%;
  width:45%;
  font-size: 18px;
  border-radius: 15px;
  border: none;
  outline: none;
  color: white;
  background-color: #ff6c6c;
}
.login-in button{
  margin-bottom: 15px;
}
.login-in-bottom{
  height:10%;
  width:60%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.login-in a{
  text-decoration: none;
}
 .todo-container{
  height:100vh;
  width:100vw;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  background-color: #282c34;
}
.header {
  min-height: 100vh;
  width:70%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 25px;
  color: white;
}
.header h1{
  margin-bottom: 20px;
}
.todo-input {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  width:55%;
  height:10%;
}

.todo-input input {
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-right: 10px;
  flex: 1;
  height:75%;
}

.todo-input button {
  padding: 10px 20px;
  font-size: 16px;
  background-color: #61dafb;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  height:75%;
}

.todo-list {
  list-style: none;
  padding: 0;
}

.todo-list li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #333;
  padding: 10px;
  border-radius: 4px;
  margin-bottom: 10px;
  min-width: 420px;
}

.todo-list li button {
  background-color: #ff6c6c;
  color: white;
  border: none;
  padding: 10px;
  border-radius: 4px;
  cursor: pointer;
}
.deleteall-button, .logout-btn{
 height:50px;
 width:180px;
 display: flex;
 justify-content: center;
 align-items: center;
 border: none;
 outline: none;
 border-radius: 18px;
 font-size: 16px;
 color: white;
 margin-left: 30px;
 margin-top: 15px;
 background-color: #ff6c6c;
}
.logout-btn a{
  text-decoration: none;
  color: white;
  font-size: 16px;
}
@media only screen and (min-width:280px) and (max-width:600px){
  html,body{
    width:100%;
    overflow-x: hidden;
  }
  .todo-container{
    width:100%;
  }
  .header {
    font-size: 20px;
  }
  .todo-input {
    margin-bottom: 10px;
    width:100%;
    height:8%;
  }
  .todo-input input {
    padding: 6px;
    font-size: 13px;
    border-radius: 4px;
    margin-right: 6px;
  }
  .todo-input button {
    padding: 6px 12px;
    font-size: 13px;
    border-radius: 4px;
  }
  .todo-list li {
    padding: 6px;
    border-radius: 4px;
    margin-bottom: 6px;
    min-width: 220px;
  }
  
  .todo-list li button {
    padding: 6px;
    border-radius: 4px;
  }
  .deleteall-button, .logout-btn{
   height:30px;
   width:80px;
   border-radius: 13px;
   font-size: 13px;
   margin-left: 20px;
   margin-top: 10px;
  }
  .logout-btn a{
    font-size: 13px;
  }
  .signup-container, .login-container{
    width:100%;
  }
  .signup-in, .login-in{
    height:60%;
    width:80%;
  }
  .signup-in h1, .login-in h1{
    height:25%;
    width:100%;
    font-size: 20px;
  }
  .signup-in form, .login-in form{
    height:75%;
    width:80%;
  }
  .signup-in form input, .login-in form input{
    font-size: 15px;
  }
  .login-in-bottom {
  width:100%;
  }
}